<template>
    <div class="container">
        <Navbar />
        <div class="bg-image"></div>
        <div class="bg-tint"></div>
        <div class="form-container">
            <h2>Rekisteridy</h2>
            <div v-if="errorMessageSend" class="errormessage">
                <p> Lomakkessa havaittiin seuraavia virheit:<br>
                    <p v-for="(error, index) in errors" :key="index">
                        * {{ error }}
                    </p>
                </p>
            </div>
            <form @submit.prevent="registrateUser" method="post">
                <div>
                    <div>
                        <label for="username">Kyttjnimi</label>
                        <input type="text" name="username" v-model="username">
                    </div>
                    <div>
                        <label for="email">Shkposti *</label>
                        <input type="text" name="email" v-model="email">
                    </div>
                    <div>
                        <label for="firstname">Etunimi *</label>
                        <input type="text" name="firstname" v-model="firstname">
                    </div>
                    <div>
                        <label for="lastname">Sukunimi *</label>
                        <input type="text" name="lastname" v-model="lastname">
                    </div>
                    <div>
                        <label for="password">Salasana *</label>
                        <input type="password" name="password" v-model="password">
                    </div>
                    <div>
                        <label for="checkpassword">Varmista salasana *</label>
                        <input type="password" name="checkpassword" v-model="checkPassword"
                        :class="{ denied: passwordApproved == false }">
                    </div>
                </div>
                <button type="submit">Lhet</button>
            </form>
        </div>
    </div>
</template>

<script>
import userService from '../services/user.js';
import Navbar from './Navbar.vue';

export default {
    name: 'Register',
    data() {
        return {
            passwordRules: [
                {
                    message: 'Salasanan pituus pitisi olla vhintn 8 merkki.',
                    regex: /.{8,}/
                },
                {
                    message: 'Salasanassa pitisi esiinty vhintn yksi numero.',
                    regex: /[0-9]+/
                },
                {
                    message: 'Salasanassa pitisi esiinty vhintn yksi iso kirjain.',
                    regex: /[A-Z]+/
                }
            ],
            emailRules: [
                {
                    message: 'Shkposti on virheellinen.',
                    regex: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                }
            ],
            errors: [],
            username: null,
            email: null,
            firstname: null,
            lastname: null,
            password: null,
            checkPassword: null,
            errorMessageSend: false
        }
    },
    methods: {
        registrateUser: async function() {
            this.errors = [];

            if(this.username && (this.username.length > 6))
                this.errors.push('Kyttjnimi voi olla enintn kuusi merkki.');

            // Check user inputs from form
            if(!this.email || this.email == '')
                this.errors.push('Shkposti vaaditaan');

            // If user has entered an email, check if it has a proper structure
            else {
                const emailErrors = this.validateUserInput(this.emailRules, this.email);

                if(emailErrors)
                    for(let i = 0; emailErrors.length > i; i++)
                        this.errors.push(emailErrors[i]);
            }

            if(!this.firstname || this.firstname == '')
                this.errors.push('Etunimi vaaditaan.');

            if(!this.lastname || this.lastname == '')
                this.errors.push('Sukunimi vaaditaan.');

            if(!this.password || this.password == '')
                this.errors.push('Salasana vaaditaan.');

            // If user has entered a password, check if it has a proper structure
            else {
                const passwordErrors = this.validateUserInput(this.passwordRules, this.password);

                if(passwordErrors)
                    for(let i = 0; passwordErrors.length > i; i++)
                        this.errors.push(passwordErrors[i]);

                if(!this.checkPassword)
                    this.errors.push('Varmenna salasanasi.');

                else if(this.checkPassword !== this.password)
                    this.errors.push('Salasanat eivt tsm.');
            }

            // When form is correctly filled pack credentials in an object
            if(this.errors.length == 0) {
                this.errorMessageSend = false;

                const credentials = {
                    username: this.username,
                    firstname: this.firstname,
                    lastname: this.lastname,
                    email: this.email,
                    password: this.password
                };

                // Then credentials are send to the database
                await userService.processCredentials(credentials, 'register');

                // Finally user is moved to another page
                this.$router.push('/login');
            }

            else
                this.errorMessageSend = true;
        },
        validateUserInput: function(rules, bindData) {
            let errors = [];

            /**
             * Function uses rules (declared previously) to check
             * if input is filled correctly (bindData) with regex
             * and if it's not the function returns error(s) (included in rules object)
             */
            for(let rule of rules)
                if(!rule.regex.test(bindData))
                    errors.push(rule.message);

            if(errors.length == 0)
                return false;
            else
                return errors;
        }
    }, setup() {
        return { Navbar }
    }
}
</script>

<style scoped>
    nav {
        display: flex;
        background-color: #444444;
        width: 100%;
        position: fixed;
        top: 0;
        left: 0;
        height: 8%;
        overflow: hidden;
        z-index: 100;
        justify-content: center;
        border-bottom: 0.7vh solid #76B2BA;
    }

    nav > img {
        height: 70%;
        margin: auto;
    }

    .bg-image {
        background-image: url('../assets/start_bg.jpg');
        background-size: cover;
        background-repeat: no-repeat;
        height: 95vh;
        filter: blur(3px);
        z-index: 0;
    }

    .bg-tint {
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background-color: #76B2BA;
        opacity: 0.4;
        z-index: 1;
    }

    .form-container {
        position: absolute;
        top: 10%;
        right: 10%;
        left: 10%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        min-height: 70vh;
        z-index: 50;
    }

    form {
        display: flex;
        flex-wrap: wrap;
        width: 750px;
        margin: 0 auto;
    }

    form div {
        margin: 0 20px;
        display: inline-block;
    }

    input {
        width: 100%;
        padding: 5px;
        font-size: 18px;
        border: 1px solid #000;
        border-radius: 2px;
    }

    label {
        padding: 2px 0;
    }

    label, input {
        flex: none;
        display: block;
        color: #000;
        text-align: left;
    }

    button {
        margin: 10px auto;
    }

    .denied {
        border-color: red;
    }

    .errormessage {
        width: 500px;
        margin: 15px auto;
        background-color: red;
        padding: 1px 15px;
        color: white;
        border-radius: 5px;
        text-align: left;
        padding: 10px;
    }

    .errormessage p {
        padding: 1px 5px;
        margin: 0;
    }

    @media only screen and (max-width: 1060px) {
        form div {
            margin: 0 auto;
            width: 90%;
        }

        form {
            width: 80%;
        }

        .errormessage {
            max-width: 65%;
        }
    }
</style>