<template>
    <div class="container">
    <Navbar />
    <div class="bg-image"></div>
    <div class="bg-tint"></div>
        <div id="login">
            <label for="username">Kyttjtunnus: </label>
            <input type="text" name="username" id="username" v-model="username"><br>
            <label for="password">Salasana: </label>
            <input type="password" name="password" id="password" v-model="password"><br>
            <button id="loginButton" @click="login">Kirjaudu</button>
            <router-link to="/register" id="register">REKISTERIDY</router-link>
        </div>
    </div>
</template>

<script>
import userService from '../services/user.js';
import { ref } from 'vue';
import Navbar from './Navbar.vue';
import { useStore } from 'vuex'
import jwt from 'jsonwebtoken';

export default {
    name: 'Login',
    components: {
        Navbar
    },
    setup() {
        const username = ref('');
        const password = ref('');
        const store = useStore();

        const login = async () => {
            const credentials = { username: username.value, password: password.value };
            // Post username and password to backend
            const response = await userService.processCredentials(credentials, 'login');

            // If login is successful, jwt token should have been sent from backend and saved to cookie
            if (response.token) {
                console.log(response.token);
                document.cookie=`token=${response.token}`;
                const decodedToken = jwt.decode(response.token);
                store.commit('setLoggedUser', decodedToken.username);
            }
            else
                console.log('Ei tokenia');
        }

        return { username, password, login };
    }

}
</script>

